/* eslint-disable no-undef */

class Card extends HTMLElement {
	constructor() {
		super();
		const data = {
			id: this.getAttribute("id"),
			image: this.getAttribute("image"),
			rating: this.getAttribute("rating"),
			name: this.getAttribute("name"),
			city: this.getAttribute("city"),
			desc: this.getAttribute("desc"),
		};
		this.innerHTML = `<article class="card">
		<div class="card-image" id="${data.id}">
			<img class="image lazyload" data-src="${data.image}" alt="${data.name}" />
			<span class="rating">
				<span class="icon">
					<svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" class="bi bi-star-fill"
						viewBox="0 0 16 16">
						<path
							d="M3.612 15.443c-.386.198-.824-.149-.746-.592l.83-4.73L.173 6.765c-.329-.314-.158-.888.283-.95l4.898-.696L7.538.792c.197-.39.73-.39.927 0l2.184 4.327 4.898.696c.441.062.612.636.282.95l-3.522 3.356.83 4.73c.078.443-.36.79-.746.592L8 13.187l-4.389 2.256z" />
					</svg>
				</span>
				${data.rating}</span>
		</div>
		<span class="card-title">${data.name}</span>
		<span class="card-subtitle text-mute">${data.city}</span>
		<div class="card-description">
			<p>${data.desc}...</p>
		</div>
		<div class="card-footer">
			<a href="detail.html?id=${data.id}" class="btn btn-primary">Detail</a>
		</div>
	</article>`;
	}
}

customElements.define("x-card", Card);
